import VueRouter from 'vue-router';
import Base64Text from './components/Base64Text.vue'
import Base64File from './components/Base64File.vue'

export default () => VueRouter.createRouter({
    history: VueRouter.createWebHistory(),
    routes: [
        {
            path: '/',
            component: Base64Text
        },
        {
            path: '/file',
            component: Base64File
        },
    ]
});